@import url(https://fonts.googleapis.com/css2?family=Caveat:wght@700&family=Dancing+Script:wght@700&display=swap);
.title{font-family:'Dancing Script', cursive;font-weight:bold;font-size:60px;color:red;text-align:center}.subHeader{font-family:'Caveat', cursive;font-weight:bold;font-size:40px;color:orange;text-align:center}.row{display:-webkit-flex;display:flex}.column{-webkit-flex:50% 1;flex:50% 1}.button{margin-top:20px;margin-left:auto;margin-right:auto;display:block}img[alt=freeCodeCamp]{width:200px}#preview{padding:0 1rem}#preview blockquote{border-left:3px solid #224b4b;color:#224b4b;padding-left:5px;margin-left:25px}#preview>p>code{padding:3px}#preview code{background:white;font-size:0.875rem;font-weight:bold}#preview pre{display:block;overflow:auto;background:white;padding:5px;line-height:1.2}#preview table{border-collapse:collapse}#preview td,#preview th{border-style:solid;border-width:2px;border-color:black;padding-left:5px;padding-right:5px}#preview img{display:block;max-width:90%;margin:2rem auto}

